* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: DM Sans;
}

html {
  scroll-behavior: smooth;
}

.container {
  max-width: 480px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.icon {
  fill: currentColor;
  stroke: currentColor;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

.circle-btn {
  height: 18px;
  width: 18px;
  cursor: pointer;
  border: none;
  border-radius: 50%;
  transition: background-color .3s;
}

.circle-btn--products {
  height: 40px;
  width: 40px;
  background-color: #fff;
}

.circle-btn__arrow {
  stroke: #d41443;
  stroke-width: 4px;
}

.circle-btn__arrow--products {
  margin: 0 auto;
  display: block;
}

.circle-btn:hover {
  background-color: #ecebeb;
}

.button, .modal-form__button {
  color: #fff;
  cursor: pointer;
  background-color: #d41443;
  border: none;
  border-radius: 2em;
  padding: .9em 1.7em;
  font-family: DM Sans;
  transition: background-color .2s, transform .2s, box-shadow .2s;
}

.button:hover, .modal-form__button:hover, .button:focus, .modal-form__button:focus {
  background-color: #c7133e;
  transform: translateY(-3px);
  box-shadow: 1px 1px 7px 1px #d4144399;
}

.button:active, .modal-form__button:active {
  transform: translateY(-1px);
  box-shadow: 0 5px 10px #0003;
}

.button--reversed {
  color: #d41443;
  background-color: #fff;
}

.button--reversed:hover, .button--reversed:focus {
  background-color: #ecebeb;
  box-shadow: 1px 1px 7px 1px #16151599;
}

.button--reversed:active {
  box-shadow: 0 5px 10px #c4373733;
}

.button--small {
  font-size: .5rem;
}

.button--mid, .modal-form__button {
  font-size: .875rem;
  font-weight: 700;
}

.button--big {
  font-size: 1rem;
  font-weight: 700;
}

.button--menu {
  width: 100%;
  max-width: 210px;
  margin-bottom: 19px;
}

.button a, .modal-form__button a {
  color: inherit;
  text-decoration: none;
}

.button__chevron {
  stroke: currentColor;
  vertical-align: middle;
  margin-left: 13px;
}

.menu-toggle {
  cursor: pointer;
  background: none;
  border: none;
}

@media screen and (min-width: 768px) {
  .button--mid, .modal-form__button {
    padding: .85em 2.3em;
    font-size: 1rem;
  }

  .button--menu {
    max-width: 186px;
  }
}

@media screen and (min-width: 1280px) {
  .button--small {
    padding: .85em 1.6em;
    font-size: .875rem;
  }
}

