.menu-backdrop {
  -webkit-backdrop-filter: blur(1px);
  backdrop-filter: blur(1px);
  width: 100%;
  height: 100%;
  z-index: 4;
  opacity: 1;
  background: #ffffff40;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(0);
}

.is-hidden-menu {
  opacity: 0;
  transform: translateX(100%);
}

.menu-container {
  z-index: 400;
  text-align: center;
  width: 250px;
  height: 100%;
  opacity: 0;
  background-color: #e17992;
  padding-top: 60px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
  box-shadow: -8px 0 20px #00000040;
}

.menu-container.is-open {
  opacity: .9;
  transform: translateX(0);
}

.js-close-menu {
  color: #fff;
  transition: color .3s;
  position: absolute;
  top: 23px;
  right: 28px;
}

.js-close-menu:hover, .js-close-menu:focus {
